import React from 'react'
// import Moment from 'react-moment';
// import classNames from 'classnames';
import { Link } from 'react-router-dom';

export default function LaunchItem({launch:{flight_number, mission_name, launch_date_local, launch_success}}) {
    return (
        <div className="card card-body mb-3">
             <div className="row">
                 <div className="col-md-9">
                        <h4>Mission : 
                        <span>{ mission_name }</span>
                        </h4>
                     <p>Date: { launch_date_local }</p>
                        {/* <p>Date: <Moment format="YYYY-MM-DD HH:MM">{ launch_date_local }</Moment></p> */}
                 </div>
                 <div className="col-md-3">
                     <Link  to={`/launch/${flight_number}`} variant="outlined" className="btn btn-secondary" style={{color:'white'}}>
                          Launch Details
                     </Link>
                 </div>
             </div>
        </div>
    )
}
